<template>
     <div class="cell" @click="onClickCell">
        {{text}}
    </div>
</template>

<script>
export default {
  props: ["n", "result"],
  data() {
    return {
      text: null
    };
  },
  methods: {
    onClickCell: function() {
      if (null != this.text || null != this.result) return;
      this.text = this.n % 2 == 0 ? "O" : "X";
      this.$emit("click", this.text);
    }
  }
};
</script>

<style>
.cell {
  width: 50px;
  height: 50px;
  border: 1px solid #000000;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>


