<template>
  <div id="app">
      <div>n的值为：{{n}}</div>
      <div>
        <div class="row">
            <Cell :n="n" :result="result" @click="onClickCellMain(0,$event)"/>
            <Cell :n="n" :result="result" @click="onClickCellMain(1,$event)"/>
            <Cell :n="n" :result="result" @click="onClickCellMain(2,$event)"/>
        </div>
        <div class="row">
            <Cell :n="n" :result="result" @click="onClickCellMain(3,$event)"/>
            <Cell :n="n" :result="result" @click="onClickCellMain(4,$event)"/>
            <Cell :n="n" :result="result" @click="onClickCellMain(5,$event)"/>
        </div>
        <div class="row">
            <Cell :n="n" :result="result" @click="onClickCellMain(6,$event)"/>
            <Cell :n="n" :result="result" @click="onClickCellMain(7,$event)"/>
            <Cell :n="n" :result="result" @click="onClickCellMain(8,$event)"/>
        </div>
     </div>
      <div>{{null==result?"比赛结果未出":"比赛结束，胜利的是："+result}}</div>
  </div>
</template>

<script>
import Cell from "./components/Cell.vue";
export default {
  name: "App",
  components: { Cell },
  data() {
    return {
      n: 0,
      map: [[null, null, null], [null, null, null], [null, null, null]],
      result: null
    };
  },
  methods: {
    onClickCellMain: function(i, text) {
      this.map[Math.floor(i / 3)][i % 3] = text;
      this.n = this.n + 1;
      this.judge();
    },
    judge() {
      for (let i = 0; i < 3; i++) {
        if (
          null != this.map[i][0] &&
          this.map[i][0] == this.map[i][1] &&
          this.map[i][0] == this.map[i][2]
        ) {
          this.result = this.map[i][0];
        }
      }

      for (let j = 0; j < 3; j++) {
        if (
          null != this.map[0][j] &&
          this.map[0][j] == this.map[1][j] &&
          this.map[0][j] == this.map[2][j]
        ) {
          this.result = this.map[0][j];
        }
      }

      if (
        null != this.map[0][0] &&
        this.map[0][0] == this.map[1][1] &&
        this.map[0][0] == this.map[2][2]
      ) {
        this.result = this.map[0][0];
      }
      if (
        null != this.map[0][2] &&
        this.map[0][2] == this.map[1][1] &&
        this.map[0][2] == this.map[2][0]
      ) {
        this.result = this.map[0][2];
      }
    }
  }
};
</script>

<style>
.row {
  display: flex;
}
#app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
</style>
